import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionBackSetting , { ReduxEnumSettingBank }  from '../../../_redux/action/reduxAction.settingBank';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';










/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';

import ComponentSettingBankStep1                    from './step1/c.settingBank.step1';
// import ComponentSettingBankStep2                    from './step2/c.settingBank.step2';








import './s.settingBank.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxSettingBank' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumSettingBank.INIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumSettingBank.UPDATE_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdatePreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumSettingBank.UPDATE_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentSettingBank extends ComponentAbstract {


            constructor( props ) {
                super( props )

                this.state = {

                    step : 0

                    , data : {}


                    , form : {}


                    , result : []

                }

            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestInit();
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestInit() {
                console.log( 'ComponentSettingBank : requestInit : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBackSetting.rInit(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestInitDone( result ) {
                console.log( 'ComponentSettingBank : requestInitDone : ***  : result => ' , result );

                let data = this.state.data;
                this.setState(
                    {
                        isLoading : false
                        , data : data
                    }
                );
                this.dispatch( ReduxActionBackSetting.rRequestEnd( ) );
            }







            requestUpdatePreview() {
                console.log( 'ComponentSettingBank : requestUpdatePreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBackSetting.rUpdatePreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdatePreviewDone( result ) {
                console.log( 'ComponentSettingBank : requestUpdatePreviewDone : ***  : result => ' , result );

                let data = this.state.data;
                this.setState(
                    {
                        isLoading : false
                        , data : data
                    }
                );
                this.dispatch( ReduxActionBackSetting.rRequestEnd( ) );
            }

            requestUpdateConfirm() {
                console.log( 'ComponentSettingBank : requestUpdateConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBackSetting.rUpdateConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdateConfirmDone( result ) {
                console.log( 'ComponentSettingBank : requestUpdateConfirmDone : ***  : result => ' , result );

                let data = this.state.data;
                this.setState(
                    {
                        isLoading : false
                        , data : data
                    }
                );
                this.dispatch( ReduxActionBackSetting.rRequestEnd( ) );
            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>
                            <Paper
                                    style={ { width : '70%', margin : '0px auto' } }>


                                <div className={ ' settingBank-padding-10 ' }>
                                    <h3>Bank Setting</h3>
                                </div>


                                <div className={ ' settingBank-margin-auto settingBank-paddingTop-20 settingBank-paddingBottom-20  settingBank-maxWidth-900 ' }>

                                    <Stepper activeStep={ this.state.step }>

                                        <Step>
                                            <StepLabel>
                                                Bank Setting
                                            </StepLabel>
                                        </Step>

                                        <Step>
                                            <StepLabel> Preview </StepLabel>
                                        </Step>

                                        <Step>
                                            <StepLabel> Confirmation </StepLabel>
                                        </Step>

                                    </Stepper>

                                </div>


                                <div>

                                    {
                                        ( () => {

                                            switch ( this.state.step ) {

                                                case 0:

                                                    return (
                                                        <ComponentSettingBankStep1
                                                            onClickNext={ () => {

                                                            } }
                                                        />
                                                    );

                                                    break;

                                                case 1:

                                                    return null;

                                                    break;

                                                default:
                                                    return ( null );
                                            }

                                        } )()
                                    }

                                </div>

                            </Paper>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />

                        </div>
                )
            }
        }

    )



